.blogPostContainer {
	width: 70%;
}

.blogHeader {
	text-align: center;
}

.blogCategory {
	display: block;
	font-size: 14px;
	padding: 10px 0;
}

.postTitle {
	letter-spacing: 0.5px;
	text-transform: capitalize;
	font-size: 30px;
	margin: 0;
	font-weight: 400;
}

.postedBy {
	display: block;
	font-size: 15px;
	font-style: italic;
	padding: 10px 0;
	color: #6c6c6c;
}

.postImageContainer {
	width: 100%;
	padding: 5px;
	box-sizing: border-box;
}

.postImageContainer img {
	width: 100%;
	height: 100%;
}

.postContent {
	margin: 10px;
}

.postContent h3 {
	font-weight: 400;
	color: #333333;
}

.postContent p {
	font-size: 14px;
	color: #6c6c6c;
	font-weight: 300;
	letter-spacing: 1px;
	padding-bottom: 20px;
}
