.galleryPost {
	display: flex;
	justify-content: space-between;
}

.mainImageWrapper {
	width: 100%;
	height: 450px;
}

.mainImageWrapper img {
	width: 100%;
	max-height: 100%;
	object-fit: cover;
}

.sideImageWrapper div {
	width: 100%;
	height: 450px;
}

.sideImageWrapper div img {
	width: 100%;
	max-height: 100%;
	object-fit: cover;
}
